.Header__Container{
    width: 100%;
    height: 100px;
    background-color: var(--red);
    display: flex;
    align-items: center;
}

.Header__Logo {
    height: 80px;
    margin-left: 32px;
}

.Header__Link {
    font-family: League Gothic;
    font-size: 40px;
    color: var(--light-pink);
    margin: 0 32px;
    text-decoration: none;
}

.Header__Icon { 
    width: 32px;
    margin-right: 8px;
}

.Header__Button {
    font-family: League Gothic;
    font-size: 40px;
    color: var(--light-pink);
    margin: 0 32px;
    display: inline;
    position: relative;
    text-decoration: none;
}

.Header__Actions {
    margin-left: auto;
}

.Header__Counter {
    font-size: 20px;    
    background-color: var(--light-pink);
    width: 24px;
    height: 24px;
    position: absolute;
    color: var(--light-blue);
    border-radius: 12px;
    text-align: center;
    right: -20px;
    top: -10px;
    vertical-align: middle;
}


